<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <i class="back" @click="back"></i>
        <span class="text">新闻详情</span>
      </div>
      <div class="content-wrapper">
        <scroll class="content">
          <div>
            <p class="title">{{title}}</p>
            <p class="time">{{creationTime}}</p>
            <div class="main-content" v-html="content"></div>
          </div>
        </scroll>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Scroll from 'base/scroll/scroll'
  import {getLastUrl} from 'common/js/util'
  import {ERR_CODE} from 'api/config'
  import {getArticleDetail} from 'api/index'

  export default {
    data () {
      return {
        title: '',
        creationTime: '',
        content: ''
      }
    },
    created () {
      this._getArticleDetail()
    },
    methods: {
      _getArticleDetail(){
        let id = getLastUrl()
        if(!id){
          return
        }
        getArticleDetail(id).then((res) => {
          if(res.errCode === ERR_CODE){
            this.title = res.data.title
            this.creationTime = res.data.creationTime
            this.content = res.data.content
          }
        })
      },
      back() {
        this.$router.back()
      }
    },
    components: {
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter-active, .slide-leave-active
    transition all 0.3s

  .slide-enter, .slide-leave-to
    transform translate3d(100%, 0, 0)

  .wrapper
    position absolute
    left 0
    right 0
    top 0
    bottom 0
    z-index 1
    background $color-background
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .back
        position absolute
        background url("~common/image/back.png") no-repeat
        background-size contain
        width 8px
        height 15px
        left 15px
        top 15px
        &:before
          position absolute
          left -10px
          right -10px
          top -10px
          bottom -10px
          content ''
      .text
        color $color-header-text
        font-size $font-size-large
        font-weight bold
    .content-wrapper
      position fixed
      left 0
      right 0
      top 44px
      bottom 0
      .content
        width 100%
        height 100%
        overflow hidden
        margin-top 10px
        .title
          margin 20px 40px 0 40px
          font-size $font-size-large-x
          color #1a1a1a
          text-align center
          line-height 32px
        .time
          text-align center
          margin-top 10px
          margin-bottom 20px
          color #808080
          font-size $font-size-small
        .main-content
          margin 14px 14px 30px 14px
          line-height 24px
          text-indent 2rem

</style>



